.perform-action {
  max-width: 100%;
  padding: 1rem;
  box-sizing: border-box;
}

.nft-display-container {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(150px, 1fr));
  gap: 1rem;
  justify-items: center;
}

.nft-display {
  width: 100%;
  max-width: 150px;
  margin: 0 auto;
}

@media (max-width: 768px) {
  .nft-display-container {
    grid-template-columns: repeat(2, minmax(100px, 1fr));
    gap: 0.5rem;
  }

  .nft-display {
    max-width: 120px;
  }
}

@media (max-width: 480px) {
  .nft-display-container {
    grid-template-columns: 1fr;
    gap: 0.5rem;
  }

  .nft-display {
    max-width: 100px;
  }
}

.progress-container {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.progress-label {
  text-align: center;
  margin-top: 0.5rem;
}

.refill-button {
  margin-top: 0.5rem;
}

.nft-image {
  width: 100%;
  height: auto;
  border-radius: 8px;
  margin-top: 0.5rem;
}

select {
  margin-bottom: 0.5rem;
}
