.PlotStatus {
  max-width: 600px;
  margin: 0 auto;
  padding: 20px;
  text-align: left;
}

.plot-card {
  padding: 15px;
  border: 1px solid #ccc;
  border-radius: 8px;
  margin-bottom: 15px;
  background-color: #f9f9f9;
}

.plot-card p {
  margin: 5px 0;
  font-size: 16px;
}

.error-message {
  color: red;
  text-align: center;
}

@media (max-width: 768px) {
  .PlotStatus {
    padding: 15px;
  }

  .plot-card {
    padding: 10px;
  }
}
